import styled from 'styled-components';
import Tooltip from '../Tooltip/Tooltip';
import { MD5 } from '../../utils/utils';


const Wrapper = styled.div`
  border-bottom : 1px dashed #93A1B8;
  width : max-content;
  cursor : pointer;
  .tip-content {
    color : #E0ECFF;
    font-size :12px;
    border-radius: 8px;
    padding : 12px;
  }
`

export default function UnderlineText({text,id = MD5(text) ,tip}){
  const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
  return(
    <Wrapper data-tip  data-for={id} data-event={isMobile ? 'click' : undefined}>
        {text}
      {tip &&<Tooltip id={id} width='256' place='bottom' borderRadius={8}>
        <div className='tip-content'>{tip}</div>
      </Tooltip>}
    </Wrapper>
  )
}